.card {
    width: 100%;
    height: auto;
    padding: 0rem 2rem;
    background: white;
    display: block;
}

.grid {
    display: grid;
    grid-template-columns: 15% 85%;
}

.cards {
    width: 100%;
    height: 15rem;
    padding: 0rem 2rem;
}

.element {
    width: 100%;
    height: 13rem;
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
    background: forestgreen;
}

.cards h1 {
    color: forestgreen;
}

.cardsGrid {
    display: grid;
    grid-template-columns: 65% 35%;
}

.cardText {
    width: 100%;
    padding: 2rem 0;

}