.app {
    width: 50%;
    height: 100vh;
    padding: 0;
    margin: 0 auto;
}

.background {
    background: url('../../assets/receiption.jpg');
    background-size: cover;
    background-position: center;
}

.buttonsContainer {
    width: 25%;
    height: 100vh;
    background: rgba(255, 255, 255, 0.4);
    padding: 10rem 3rem;
    margin-left: 4rem;
}

.btn {
    width: 130px;
    height: 130px;
    border-radius: 50%;
    display: block;
    padding: 3rem 1rem;
    margin: 0 auto;
    margin-bottom: 1rem;
    border: 3px solid white;
    text-align: center;
    color: white;
    text-decoration: none;
}

.green {
    background: forestgreen;
}

.blue {
    background: royalblue;
}

.yellow {
    background: goldenrod;
}

.red {
    background: crimson;
}

.logo {
    width: 200px;
    height: 6rem;
    background: forestgreen;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    position: absolute;
    margin-top: 3rem;
    padding: 1rem 3rem;
}

.logo img {
    width: 50%;
}

.form {
    padding: 0rem 2rem;
}

.formContainer {
    padding: 1rem;
}

.formContainer input[type="text"],
[type="tel"],
[type="password"] {
    border: none;
    border-bottom: 1px solid forestgreen;
    border-radius: 0;
    padding: 1rem 0;

}

.formContainer select {
    border: none;
    border-bottom: 1px solid forestgreen;
    border-radius: 0;
    padding: 1rem 0;

}

.expandBtn {
    padding: 1rem;
}




/* phone-only */
@media (max-width: 599px) {
    .app {
        width: 100%;

    }

}

/* .tablet-portrait-up  */
@media (min-width: 600px) {
    .app {
        width: 100%;

    }
}

/* tablet-portait-only */
@media (min-width: 600px) and (max-width: 899px) {
    .app {
        width: 100%;

    }
}

/* tablet-landscape-up */
@media (min-width: 900px) {
    .app {
        width: 100%;

    }
}

/* tablet-landscape-only */
@media (min-width: 900px) and (max-width: 1199px) {
    .app {
        width: 50%;

    }
}

/* .desktop-up */
@media (min-width: 1300px) {
    .app {
        width: 50%;

    }
}

/* desktop-only */
@media (min-width: 1300px) and (max-width: 1799px) {
    .app {
        width: 50%;

    }
}

/* big-desktop-up */
@media (min-width: 1800px) {
    .app {
        width: 50%;

    }
}