.navbar {
    width: 13%;
    height: 100vh;
    position: fixed;
    float: left;
}

.logo {
    width: 100%;
    height: 5rem;
    padding: .5rem;
    overflow: hidden;
    text-align: center;
    margin-bottom: 2rem;
}

.logo img {
    width: 35%;
}

.url {
    width: 100%;
    height: auto;
    display: block;
    padding: 1rem;
}

.url ul {
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
}

.url ul li {
    width: 100%;
    padding: 0;
    margin: 0;
    line-height: 2.5rem;
}

.url ul li a {
    text-decoration: none;
    color: dimgray;
    font-size: 13px;
}