.app {
    width: 100%;
    height: 100vh;
    background: url('../../../assets/bg.jpg');
    background-size: cover;
    padding: 2rem;
}

.logo {
    width: 100%;
    height: 10rem;
    overflow: hidden;
}

.logo img {
    width: 5%;
}

.loginContainer {
    width: 40%;
    height: auto;
    display: block;
    padding: 1rem;
    margin: 0 auto;
}

.inputContainer {
    width: 100%;
    height: auto;
    display: block;
    padding: .5rem;
}

.inputContainer input {
    padding: 1rem;
}

.inputContainer input[type="email"],
[type="password"] {
    border-radius: 0;
    border: none;
    border-bottom: 1px solid forestgreen;
}

.contents {
    width: 87%;
    height: auto;
    display: block;
    margin-left: 13%;
    float: left;

}

.overlay {
    width: 100%;
    height: auto;
    min-height: 20rem;
    display: block;
    padding: 1rem;
    background: linear-gradient(90deg, rgba(57, 204, 149), rgba(58, 205, 198));

}

.cardContainer {
    width: 100%;
    height: auto;
}

.cardGrid {
    display: grid;
    grid-template-columns: 23% 23% 23% 23%;
    grid-gap: 2%;
}

.purple {
    background: #F2EBFB;
    color: #713BDB;
}

.cyan {
    background: #EEF9FF;
    color: #49B8FF;
}

.gold {
    background: #FEF3EF;
    color: #FE8A57;
}

.red {
    background: #FFEFF2;
    color: #FD5181;
}

.title {
    width: 100%;
    height: 3rem;
    color: white;
}